import React from "react";
import Parser from "./parser";

class Mainquestion extends React.Component {
  render() {
    if (this.props.html) {
      return (
        <div className="da-page-header">
          <h1 className="h3" id="daMainQuestion">
            {Parser(this.props.html)}
          </h1>
          <div className="daclear"></div>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default Mainquestion;
