import React from "react";
import Form from "react-bootstrap/Form";
import Mainquestion from "./Mainquestion";
import Subquestion from "./Subquestion";
import Fields from "./Fields";
import Buttons from "./Buttons";
import { connect } from "react-redux";
import {
  writeAnswers,
  submitAnswers,
  getData,
  setSubmitted,
  submitData,
} from "../actions/actions";
import PropTypes from "prop-types";
import Parser from "./parser";

class Question extends React.Component {
  static propTypes = {
    data: PropTypes.object.isRequired,
    answers: PropTypes.object.isRequired,
    writeAnswers: PropTypes.func.isRequired,
    submitAnswers: PropTypes.func.isRequired,
    getData: PropTypes.func.isRequired,
  };

  onSubmit = (e) => {
    e.preventDefault();
    //validation here
    this.props.setSubmitted(true);
    this.props.submitData();
  };

  componentDidMount() {
    //console.log("componentDidMount");
    console.log("user code is " + window.daUserCode);
    this.props.getData();
    this.props.setSubmitted(false);
    //console.log("ran getData");
  }

  render() {
    if (!this.props.data.question) {
      return null;
    }
    if (this.props.data.showHelp && this.props.data.question.help) {
      var i = 0;
      return (
        <React.Fragment>
          {this.props.data.question.helpText.map((item) => (
            <React.Fragment key={"help" + i}>
              {item.heading ? (
                <div className="da-page-header">
                  <h1 className="h3">{Parser(item.heading)}</h1>
                </div>
              ) : null}
              <div>{Parser(item.content)}</div>
            </React.Fragment>
          ))}
        </React.Fragment>
      );
    }
    return (
      <Form onSubmit={this.onSubmit}>
        <Mainquestion html={this.props.data.question.questionText} />
        <Subquestion html={this.props.data.question.subquestionText} />
        <Fields />
        <Buttons />
        <div>Here are the answers: {JSON.stringify(this.props.answers)}</div>
      </Form>
    );
  }
}

const mapStateToProps = (state) => ({
  submitted: state.submitted,
  answers: state.answers,
  data: state.data,
});

export default connect(mapStateToProps, {
  writeAnswers,
  submitAnswers,
  getData,
  setSubmitted,
  submitData,
})(Question);
