export const writeAnswers = (data) => ({
  type: WRITE_ANSWERS,
  payload: {
    data,
  },
});

export const submitAnswers = (data) => ({
  type: SUBMIT_ANSWERS,
  payload: {
    data,
  },
});

var yaml_filename = "docassemble.demo:data/questions/questions.yml";

const encodeGetParams = (p) =>
  Object.entries(p)
    .map((kv) => kv.map(encodeURIComponent).join("="))
    .join("&");

export const getData = () => (dispatch, getState) => {
  //console.log("Inside of getData");
  var state = getState();
  const params = {
    i: yaml_filename,
    session: state.data.session || "",
    secret: state.data.secret || "",
    user_code: state.data.user_code || "",
  };
  axios
    .get("http://localhost/api/interview?" + encodeGetParams(params))
    .then((res) => {
      if ("activeElement" in document) {
        document.activeElement.blur();
      }
      dispatch({
        type: GET_DATA,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(
        returnErrors(
          err.response ? err.response.data : err,
          err.response ? err.response.status : 0,
          "danger"
        )
      )
    );
};

export const goBack = () => (dispatch, getState) => {
  var state = getState();
  var data = {
    i: yaml_filename,
    session: state.data.session,
    secret: state.data.secret,
    user_code: state.data.user_code,
    command: "back",
  };
  axios
    .post("http://localhost/api/interview", data)
    .then((res) => {
      if ("activeElement" in document) {
        document.activeElement.blur();
      }
      dispatch({
        type: GET_DATA,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(
        returnErrors(
          err.response ? err.response.data : "Error",
          err.response ? err.response.status : 0,
          "danger"
        )
      )
    );
};

export function setSubmitted(submittedValue) {
  return {
    type: SET_SUBMITTED,
    payload: submittedValue,
  };
}

export function setHelp(value) {
  return {
    type: SET_HELP,
    payload: value,
  };
}

export const toggleHelp = () => (dispatch, getState) => {
  var state = getState();
  dispatch({
    type: SET_HELP,
    payload: { showHelp: state.data.showHelp ? false : true },
  });
};

export const toggleSource = () => (dispatch, getState) => {
  var state = getState();
  dispatch({
    type: SET_SOURCE,
    payload: { showSource: state.data.showSource ? false : true },
  });
};

