export const submitData = () => (dispatch, getState) => {
  //console.log("submit data");
  var state = getState();
  var data = {
    i: yaml_filename,
    session: state.data.session,
    secret: state.data.secret,
    user_code: state.data.user_code,
    variables: state.answers,
  };
  axios
    .post("http://localhost/api/interview", data)
    .then((res) => {
      if ("activeElement" in document) {
        document.activeElement.blur();
      }
      dispatch({
        type: GET_DATA,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(
        returnErrors(
          err.response ? err.response.data : "Error",
          err.response ? err.response.status : 0,
          "danger"
        )
      )
    );
};
