import {
  WRITE_ANSWERS,
  SUBMIT_ANSWERS,
  GET_DATA,
} from "../actions/actionTypes.js";

const initialState = {};

function answers(state = initialState, action) {
  switch (action.type) {
    case WRITE_ANSWERS:
      return {
        ...state,
        ...action.payload.data,
      };
    case SUBMIT_ANSWERS:
      return {
        ...state,
        ...action.payload,
      };
    case GET_DATA:
      //console.log("Doing clear answers");
      var newstate = {};
      if (action.payload.question.fields) {
        var n = action.payload.question.fields.length;
        for (var i = 0; i < n; ++i) {
          if (
            action.payload.question.fields[i].variable_name &&
            action.payload.question.fields[i].default !== undefined &&
            action.payload.question.fields[i].default !== null
          ) {
            newstate[action.payload.question.fields[i].variable_name] =
              action.payload.question.fields[i].default;
          }
        }
      }
      return newstate;
    default:
      return state;
  }
}

export default answers;
