import React from "react";
import Button from "react-bootstrap/Button";
import { connect } from "react-redux";
import { writeAnswers } from "../actions/actions";
import Parser from "./parser";

class Buttons extends React.Component {
  constructor(props) {
    super();
    this.onClick = this.onClick.bind(this);
  }

  onClick(event) {
    //console.log("onClick:" + event.target.value);
    var the_value = event.target.value;
    if (event.target.value === "true") {
      the_value = "True";
    } else if (event.target.value === "false") {
      the_value = "False";
    } else if (event.target.value === "null") {
      the_value = "None";
    }
    this.props.writeAnswers({
      [this.props.data.question.fields[0].variable_name]: the_value,
    });
  }

  render() {
    if (this.props.data.question.questionType === "multiple_choice") {
      if (this.props.data.question.questionVariety === "radio") {
        return (
          <fieldset className="da-field-buttons">
            <div>
              <Button
                variant="primary"
                className="btn-da"
                key="button_0"
                type="submit"
                disabled={this.props.submitted}
              >
                {Parser(this.props.data.question.continueLabel)}
              </Button>
            </div>
          </fieldset>
        );
      } else if (this.props.data.question.questionVariety === "buttons") {
        var i = 0;
        return (
          <fieldset className="da-field-buttons">
            <div>
              {this.props.data.question.fields[0].choices.map((item) => (
                <React.Fragment key={"buttonfragment" + i++}>
                  <Button
                    variant="primary"
                    className="btn-da"
                    type="submit"
                    key={"button" + i}
                    disabled={this.props.submitted}
                    value={item.value}
                    onClick={this.onClick}
                  >
                    {Parser(item.label)}
                  </Button>{" "}
                </React.Fragment>
              ))}
            </div>
          </fieldset>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

const mapStateToProps = (state) => ({
  submitted: state.submitted,
  answers: state.answers,
  data: state.data,
});

export default connect(mapStateToProps, { writeAnswers })(Buttons);
