import parse, { domToReact } from "html-react-parser";
import Popover from "react-bootstrap/Popover";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";

var i = 0;

function makePopover(attribs) {
  return (
    <Popover id={"da_popover_" + i++}>
      <Popover.Content>{parse(attribs["data-content"])}</Popover.Content>
    </Popover>
  );
}

function doEval(statement) {
  return function (e) {
    console.log(statement);
    e.preventDefault();
    return false;
  };
}

const options = {
  replace: ({ attribs, children }) => {
    if (!attribs) {
      return;
    }
    if (attribs["data-js"]) {
      return (
        <a href="#js" onClick={doEval(attribs["data-js"])}>
          {domToReact(children, options)}
        </a>
      );
    }
    if (attribs.class === "daterm") {
      return (
        <OverlayTrigger
          trigger="click"
          transition={false}
          placement={attribs["data-placement"] || "auto"}
          overlay={makePopover(attribs)}
        >
          <span className="daterm">{domToReact(children, options)}</span>
        </OverlayTrigger>
      );
    }
  },
};

export default function Parser(text) {
  if (typeof text !== "string") {
    return null;
  }
  return parse(text, options);
}
