import React from "react";
import Parser from "./parser";
import Container from "react-bootstrap/Container";
import { connect } from "react-redux";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { goBack, toggleHelp, toggleSource } from "../actions/actions";
library.add(fab, fas);

class PhoneLink extends React.Component {
  render() {
    if (this.props.data.phone) {
      return (
        <Nav.Item>
          <Nav.Link
            role="button"
            href="#dahelp"
            data-target="#dahelp"
            title={this.props.data.phone.title}
            className="dapointer dahelptrigger"
          >
            <FontAwesomeIcon
              icon={["fas", "phone"]}
              className="da-chat-active"
            />
            <span className="sr-only">{this.props.data.phone.label}</span>
          </Nav.Link>
        </Nav.Item>
      );
    }
    return null;
  }
}

class ChatLink extends React.Component {
  render() {
    if (this.props.data.chat) {
      return (
        <Nav.Item>
          <Nav.Link
            href="#dahelp"
            data-target="#dahelp"
            className="nav-link dapointer dahelptrigger"
          >
            <FontAwesomeIcon icon={["fas", "comment-alt"]} />
            <span className="sr-only">{this.props.data.chat.label}</span>
          </Nav.Link>
        </Nav.Item>
      );
    }
    return null;
  }
}

class HelpLink extends React.Component {
  constructor(props) {
    super();
    this.doToggle = this.doToggle.bind(this);
  }
  doToggle(event) {
    event.preventDefault();
    this.props.toggleHelp();
    if ("activeElement" in document) {
      document.activeElement.blur();
    }
    return false;
  }
  render() {
    if (this.props.data.question.help) {
      return (
        <Nav.Item>
          <Nav.Link
            className="dapointer da-no-outline dahelptrigger"
            href="#help"
            id="dahelptoggle"
            onClick={this.doToggle}
            title={this.props.data.question.help.title}
            active={this.props.data.showHelp}
          >
            {this.props.data.question.help.label}
          </Nav.Link>
        </Nav.Item>
      );
    }
    return null;
  }
}

class SourceLink extends React.Component {
  constructor(props) {
    super();
    this.doToggle = this.doToggle.bind(this);
  }
  doToggle(event) {
    event.preventDefault();
    this.props.toggleSource();
    if ("activeElement" in document) {
      document.activeElement.blur();
    }
    return false;
  }
  render() {
    if (this.props.data.question.source) {
      return (
        <Nav.Item>
          <Nav.Link
            className="da-no-outline d-none d-md-block"
            href="#source"
            id="dasourcetoggle"
            onClick={this.doToggle}
            title={this.props.data.question.source.title}
            active={this.props.data.showSource}
          >
            {this.props.data.question.source.label}
          </Nav.Link>
        </Nav.Item>
      );
    }
    return null;
  }
}

class BackButton extends React.Component {
  constructor(props) {
    super();
    this.clickBackButton = this.clickBackButton.bind(this);
  }

  clickBackButton(event) {
    console.log("go back");
    event.preventDefault();
    this.props.goBack();
    return false;
  }

  render() {
    if (this.props.data.question.allow_going_back) {
      return (
        <Navbar.Brand href="#" key="daback" onClick={this.clickBackButton}>
          <button
            className="dabackicon text-muted dabackbuttoncolor"
            title={this.props.data.question.backTitle}
          >
            <span>
              <FontAwesomeIcon icon={["fas", "chevron-left"]} />
              <span className="daback">
                {this.props.data.question.cornerBackButton}
              </span>
            </span>
          </button>
        </Navbar.Brand>
      );
    }
    return null;
  }
}

class CornerNav extends React.Component {
  render() {
    if (!this.props.data.question.menu) {
      return null;
    }
    if (this.props.data.question.menu.items.length == 0) {
      return (
        <Nav.Link href={this.props.data.question.menu.top.href || "#"}>
          {this.props.data.question.menu.top.anchor}
        </Nav.Link>
      );
    }
    return null;
  }
}

class NavigationBar extends React.Component {
  render() {
    if (!this.props.data || !this.props.data.question) {
      return null;
    }
    return (
      <Navbar
        bg={this.props.data.question.navbarVariant}
        variant={this.props.data.question.navbarVariant}
        expand="md"
        className="fixed-top"
      >
        <Container className="danavcontainer justify-content-start">
          <BackButton data={this.props.data} goBack={this.props.goBack} />
          <Navbar.Brand
            id="dapagetitle"
            key="datitle"
            className="danavbar-title dapointer"
            href="#"
          >
            <span className="d-none d-md-block">
              {Parser(this.props.data.question.title)}
            </span>
            <span className="d-block d-md-none">
              {Parser(this.props.data.question.short_title)}
            </span>
          </Navbar.Brand>
          <Nav className="damynavbar-right">
            <SourceLink
              data={this.props.data}
              toggleSource={this.props.toggleSource}
            />
            <HelpLink
              data={this.props.data}
              toggleHelp={this.props.toggleHelp}
            />
            <PhoneLink data={this.props.data} />
            <ChatLink data={this.props.data} />
          </Nav>
          <Navbar.Toggle aria-controls="basic-navbar-nav" className="ml-auto" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ml-auto">
              <CornerNav data={this.props.data} />
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    );
  }
}

const mapStateToProps = (state) => ({
  submitted: state.submitted,
  answers: state.answers,
  data: state.data,
});

export default connect(mapStateToProps, { goBack, toggleHelp, toggleSource })(
  NavigationBar
);
