import React from "react";
import Table from "react-bootstrap/Table";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { connect } from "react-redux";
import SyntaxHighlighter from "react-syntax-highlighter";
import syntaxstyle from "react-syntax-highlighter/dist/esm/styles/hljs/stackoverflow-light.js";

class HighlightCode extends React.Component {
  render() {
    if (this.props.item.code) {
      return (
        <SyntaxHighlighter language="yaml" style={syntaxstyle}>
          {this.props.item.code}
        </SyntaxHighlighter>
      );
    }
    return null;
  }
}

class SourceFile extends React.Component {
  render() {
    if (this.props.item.source_file) {
      return (
        <p>
          <small>
            <strong>(from {this.props.item.source_file})</strong>
          </small>
        </p>
      );
    }
    return null;
  }
}

class StepHeading extends React.Component {
  render() {
    switch (this.props.item.reason) {
      case "needed":
        return (
          <h5>
            {this.props.item.reason_text + " "}
            <code>{this.props.item.variable_name}</code> at{" "}
            {this.props.item.time}
          </h5>
        );
      default:
        return (
          <h5>
            {this.props.item.reason_text} at {this.props.item.time}
          </h5>
        );
    }
  }
}

class Source extends React.Component {
  render() {
    if (this.props.data.showSource && this.props.data.question.source) {
      var part;
      if (this.props.data.showHelp) {
        part = "help";
      } else {
        part = "question";
      }
      return (
        <React.Fragment>
          <Row>
            <Col
              xl={6}
              lg={6}
              md={8}
              sm={12}
              className="offset-xl-3 offset-lg-3 offset-md-2"
            >
              <h3>Readability</h3>
              <Table>
                <thead>
                  <tr>
                    <th>Formula</th>
                    <th>Score</th>
                  </tr>
                </thead>
                <tbody>
                  {this.props.data.question.source.readability[part].map(
                    (item) => (
                      <tr key={item[0]}>
                        <td>{item[0]}</td>
                        <td>{item[1]}</td>
                      </tr>
                    )
                  )}
                </tbody>
              </Table>
            </Col>
          </Row>
          <Row>
            <Col md={12}>
              <a
                rel="noreferrer"
                target="_blank"
                href={this.props.data.question.source.varsLink}
              >
                {this.props.data.question.source.varsLabel}
              </a>
              <h3>Source code for question</h3>
              <SyntaxHighlighter language="yaml" style={syntaxstyle}>
                {this.props.data.question.source.history.source_code}
              </SyntaxHighlighter>
              <h4>How question came to be asked</h4>
              <React.Fragment>
                {this.props.data.question.source.history.steps.map((item) => (
                  <React.Fragment key={"Step_" + item.index}>
                    <StepHeading item={item} />
                    <SourceFile item={item} />
                    <HighlightCode item={item} />
                  </React.Fragment>
                ))}
              </React.Fragment>
            </Col>
          </Row>
        </React.Fragment>
      );
    }
    return null;
  }
}

const mapStateToProps = (state) => ({
  data: state.data,
});

export default connect(mapStateToProps)(Source);
