import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Question from "./components/Question";
import Alerts from "./components/Alerts";
import Source from "./components/Source";
import NavigationBar from "./components/NavigationBar";
import "./App.css";
import { Provider } from "react-redux";
//import { PersistGate } from "redux-persist/integration/react";
//import { store, persistor } from "./store";
import { store } from "./store";
import { Provider as AlertProvider } from "react-alert";

//      <PersistGate loading={null} persistor={persistor}>

const alertOptions = {
  timeout: 5000,
  position: "top center",
  containerStyle: {
    marginTop: "60px",
  },
};

export const AlertTemplate = ({ style, options, message, close }) => (
  <div className="alert alert-danger " role="alert" style={style}>
    {message}
    <button onClick={close} type="button" className="close">
      <span aria-hidden="true">&times;</span>
    </button>
  </div>
);

function App() {
  return (
    <Provider store={store}>
      <AlertProvider template={AlertTemplate} {...alertOptions}>
        <div className="da-pad-for-navbar">
          <NavigationBar />
          <Container>
            <Row>
              <Col
                xl={6}
                lg={6}
                md={8}
                className="offset-xl-3 offset-lg-3 offset-md-2"
              >
                <Question />
                <Alerts />
              </Col>
            </Row>
            <Source />
          </Container>
        </div>
      </AlertProvider>
    </Provider>
  );
}

export default App;
