import React from "react";
import Radio from "./Radio";
import { connect } from "react-redux";

class Fields extends React.Component {
  render() {
    if (this.props.data.question.questionType === "multiple_choice") {
      if (this.props.data.question.questionVariety === "radio") {
        return (
          <Radio
            name={this.props.data.question.fields[0].variable_name}
            fieldName={"_field_0"}
            choices={this.props.data.question.fields[0].choices}
            answers={this.props.answers}
          />
        );
      } else {
        return null;
      }
    } else if (this.props.data.question.questionType === "fields") {
      var output = "";
      var n = this.props.data.question.fields.length;
      for (var j = 0; j < n; ++j) {
        //var fieldName = "field_" + j;
      }
      return output;
    } else {
      return null;
    }
  }
}

const mapStateToProps = (state) => ({
  submitted: state.submitted,
  answers: state.answers,
  data: state.data,
});

export default connect(mapStateToProps)(Fields);
