import { SET_SUBMITTED, GET_DATA } from "../actions/actionTypes.js";

const initialState = null;

function submitted(state = initialState, action) {
  switch (action.type) {
    case SET_SUBMITTED:
      if (action.payload) {
        return true;
      } else {
        return false;
      }
    case GET_DATA:
      return false;
    default:
      return state;
  }
}

export default submitted;
