import React from "react";
import Form from "react-bootstrap/Form";
import { connect } from "react-redux";
import { writeAnswers } from "../actions/actions";
import Parser from "./parser";

class Radio extends React.Component {
  constructor(props) {
    super();
    this.onChangeValue = this.onChangeValue.bind(this);
  }

  onChangeValue(event) {
    this.props.writeAnswers({ [this.props.name]: event.target.value });
  }

  render() {
    var i = 0;
    return (
      <fieldset className="da-field-radio">
        <div className="mb-3" onChange={this.onChangeValue}>
          {this.props.choices.map((item) => (
            <Form.Check
              type="radio"
              value={item.value}
              label={Parser(item.label)}
              name={this.props.fieldName}
              id={this.props.fieldName + "_" + i++}
              key={this.props.fieldName + "_" + i}
              checked={this.props.answers[this.props.name] === item.value}
              onChange={this.onChangeRadio}
            />
          ))}
        </div>
      </fieldset>
    );
  }
}

const mapStateToProps = (state) => ({
  answers: state.answers,
});

export default connect(mapStateToProps, { writeAnswers })(Radio);
