import {
  GET_DATA,
  GET_CHECKIN,
  SET_HELP,
  SET_SOURCE,
} from "../actions/actionTypes.js";

const initialState = {};

function dataReduce(state = initialState, action) {
  switch (action.type) {
    case GET_DATA:
      //console.log(
      //  "In GET_DATA reducer where payload is " + JSON.stringify(action.payload)
      //);
      return {
        ...state,
        question: {},
        ...action.payload,
      };
    case GET_CHECKIN:
      return {
        ...state,
        ...action.payload,
      };
    case SET_HELP:
      return {
        ...state,
        ...action.payload,
      };
    case SET_SOURCE:
      return {
        ...state,
        ...action.payload,
      };
    default:
      return state;
  }
}

export default dataReduce;
